.carousel-item {
  height: 70vh;
}
.img {
  background: linear-gradient(
    180deg,
    rgba(255, 0, 0, 0),
    rgba(0, 0, 0, 0.5) 75%
  );
}
.DetailPanel2 {
  text-align: center;
  color: #343a40;
  min-height: 100%;
  background: linear-gradient(
    0deg,
    rgba(0, 68, 103, 0.5),
    rgba(0, 0, 0, 1) 65%
  );
  padding: 2% 10% 2% 4%;
}

.popular-header2 {
  color: #343a40;
  background-color: #fff;
  letter-spacing: 2px;
  padding-top: 1%;
  padding-bottom: 0.1%;
  line-height: 20px;
}

.popular-header2 h2 {
  filter: drop-shadow(px 2px 2px rgba(, 0, 0, 0.3));
}

.recent {
  transition-delay: 1s;
}
.recent :hover {
  transform: scale(1.02);
}
.recent img {
  filter: grayscale(100%);
  transition: all 0.5s;
}

.popular :hover {
  transform: scale(1.04);
}
.recent img:hover {
  filter: none;
}

.carousel-container button {
  margin: -6%;
}
.carousel-container {
  width: 100vh;
}

.favoritewatchlater {
  transition-delay: 1s;
}
.favoritewatchlater :hover {
  transform: scale(1.02);
}
.favoritewatchlater img {
  border-radius: 10%;
  transition: all 0.5s;
}

.favoritewatchlater img:hover {
  filter: none;
}

.popular img {
  -webkit-box-reflect: below 0 -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(90%, transparent), to(hsla(0, 0%, 98%, 0.1)));
  -webkit-filter: drop-shadow(0 0 0.75rem #000);
  filter: drop-shadow(0 0 0.75rem #000);
  transition: all 0.2s ease-in-out;
}

img {
  vertical-align: middle;
  border-style: none;
}
*,
::after,
::before {
  box-sizing: border-box;
}

.popular,
.popular-header {
  color: #fff;
  background-color: #343a40;
}
.popular {
  padding-bottom: 5%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

.UpperHeader2,
.popular-header,
.UpperHeader {
  padding-top: 90px;
}

.container {
  text-align: center;
  margin: 0 auto;
  color: #000;
}

.container a {
  color: black;
}

.container :hover {
  color: black;
}

.navbar {
  overflow: hidden;
  min-height: 60px;
}
