.navbar img {
  height: 35px;
  width: 35px;
}

.nav_links a span {
  visibility: hidden;
  align-items: center;
  margin: 0 auto;
  font-size: 0.75em;
  text-align: center;
}

.nav_links a:hover span {
  visibility: visible;
  position: relative;
  bottom: 10px;
}

.nav_links svg {
  align-items: center;
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  margin-bottom: -45px;
}

.nav_links {
  padding: 0 0 0 0;
}

.searchbar {
  margin: 0 auto;
}

.nav_icons {
  display: inline-block !important;
  margin: 0 auto;
  padding-bottom: 25px;
  padding-top: 15px;
  padding-inline-start: 0;
}

.nav_icons > a {
  display: inline-block;
  text-align: center;
  width: 48px;
  overflow: visible;
  clear: both;
}
.site {
  display: inline-block;
}

@media screen and (max-width: 990px) {
  .nav_links a span {
    visibility: visible;
    position: relative;
    bottom: 30px;
  }
  .nav_icons > a {
    margin-left: 20px;
    clear: both;
  }

  .newSearch input {
    max-width: 30vh;
    overflow: hidden;
  }
  .site {
    display: none;
  }
}
.newSearch {
  display: inline-block;
  margin-left: 15px;
}

.newSearch input {
  background-color: #41474c;
  color: aliceblue;
  border: 0;
  border-radius: 2;
  min-height: 42px;
}

::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.dropdown-toggle {
  margin: 0 auto;
  text-align: center;
}

.dropdown {
  margin-top: 20px;
}
