.registerLogin input {
  border: 0;
  box-shadow: none;
  -webkit-appearance: none;
}

.registerBox {
  background-color: blue;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.registerLogin hr {
  background-color: #343a40;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.loginBox {
  background-color: #fff;
  margin: 0 auto;
  text-align: "center";
  padding-bottom: 2%;
  margin-top: 2%;
  padding-top: 2%;
  border-radius: 10px;
  box-shadow: 3px 5px 4px #202020;
}

.loginBox h1 {
  text-align: center;
}

html {
  background-color: #343a40;
  box-sizing: none;
}
.outside {
  background-color: #343a40;
  box-sizing: none;
}

input.form-control,
input.form-control:focus {
  border: none;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  -moz-transition: none;
  -webkit-transition: none;
}
