.UpperHeader2 {
  object-position: top center;
  object-fit: cover;
  max-height: 100%;
  min-height: 8vh;
}

.DetailPanel {
  text-align: center;
  color: #fff;
  min-height: 100%;
  background: linear-gradient(
    -90deg,
    rgba(255, 0, 0, 0),
    rgba(0, 0, 0, 0.8) 15%
  );
  padding: 2% 10% 2% 4%;
}

.DetailPanel a {
  color: #fff;
  font-weight: bold;
}

.DetailPanel img {
  border-radius: 15px;
}

.player {
  margin-top: 5%;
  margin-left: 5%;
}
.video-react-video {
  box-shadow: 5px 0 15px black;
}
.cast :hover {
  color: black;
  text-decoration: none;
  -webkit-transform: translateY(-3px);
}

.carousel-item {
  height: 450px;
}

.movie {
  padding-bottom: 25px;
  transition: 0.5s;
}

.movie:hover {
  transform: scale(
    1.01
  ); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

.movie a {
  color: black;
}

.movie :hover {
  color: black;
}

.requestMovie {
  background-color: #ff8400;
  background-image: url("../../images/lpmovierequest.png");
  background-repeat: no-repeat;
  height: 300px;
  font-size: 20px;
  padding-top: 1%;
  padding-bottom: 1%;
  margin-bottom: 2%;
  color: #333;
}
